import React, {
  Component
} from 'react';
import './App.css';
import Map from './Map.js'
import Capture from './Capture.PNG'


class App extends Component {
 
  render() {
    return ( <div className = "App" >
      <header className = "App-header" >
      <p className="App-title">IBM CASINO dashboard</p>
     </header>
     <p className="title-h1"> Frigo Building</p>
    
      <Map />
     <container>
     <row debug>
     <col debug> <img src={Capture}/></col>
     
     </row>

     </container>
   
    <div flex className="plan">  </div>
          <div  flex class="card"></div>
  
     




     </div>
    );
  }
}

export default App;


